'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import Header from '@/components/Header';
import HeroSection from '@/components/HeroSection';
import WinnerInfoBar from '@/components/WinnerInfoBar';
import EntrySection from '@/components/EntrySection';
import SideMenu from '@/components/SideMenu';

// --- Type Definitions ---
declare global {
  interface Window {
    ethereum?: any;
  }
}

// --- Main Page Component ---
export default function MainPage() {
  const [account, setAccount] = useState<string | null>(null);
  const router = useRouter();

  const handleEnterRoom = (roomId: string) => {
    router.push(`/room/${roomId}`);
  };

  const connectWallet = async () => {
    if (typeof window.ethereum !== 'undefined') {
      try {
        const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
        setAccount(accounts[0]);
      } catch (err) {
        console.error('Failed to connect wallet.', err);
      }
    } else {
      alert('Please install MetaMask.');
    }
  };

  useEffect(() => {
    const handleAccountsChanged = (accounts: string[]) => {
      setAccount(accounts.length > 0 ? accounts[0] : null);
    };

    if (window.ethereum) {
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      // Check for existing accounts on initial load
      (async () => {
        const accounts = await window.ethereum.request({ method: 'eth_accounts' });
        handleAccountsChanged(accounts);
      })();

      return () => {
        // Clean up the event listener
        if (window.ethereum.removeListener) {
          window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
        }
      };
    }
  }, []);

  const participationOptions = [
    { id: '1', amount: 10, participants: '1,144,999' },
    { id: '2', amount: 100, participants: '1,144,999' },
    { id: '3', amount: 500, participants: '1,144,999' },
    { id: '4', amount: 1000, participants: '1,144,999' },
  ];

  return (
    <div className="bg-[#1a1a2e] min-h-screen text-white font-sans">
      <Header account={account} connectWallet={connectWallet} />

      <main className="px-6 md:px-12 lg:px-24 py-8">
        <HeroSection />
        <WinnerInfoBar />
        <EntrySection participationOptions={participationOptions} handleEnterRoom={handleEnterRoom} />
      </main>
      
      <SideMenu />
    </div>
  );
}
